.accueil {
  @include flex(flex, column, nowrap, space-evenly, center);
  @extend %width;
  border-radius: 20px;
  margin: 5vw auto;
  &_tim {
    @include flex(flex, column, nowrap, space-around, center);
    width: 100%;
  }
  &_titre {
    @include flex(flex, column, nowrap, space-around, center);
    font-size: 3vw;
    font-weight: 900;
    height: 20vw;
    width: 100%;
    .hello {
      font-size: 5vw;
    }
    .nom {
      margin: 1.5vw;
    }
  }
  &_img {
    @include width(25vw);
    border-radius: 30px;
    box-shadow: $bbsadow2;
    img {
      @include width(80%);
      margin-top: 10%;
    }
  }
}
.about_txt {
  margin: 10vw auto;
  padding: 4vw;
  box-shadow: $bshadow;
}
